'use client';
import ProjectCard from '@/components/ProjectCard';
import LuxuryButton from '@/components/ui/LuxuryButton';
import { useLocale } from '@/lib/i18n-client';
import type { Project } from '@/lib/types';
import { useMemo, useState } from 'react';

export default function Filters({ initial }: { initial: Project[] }) {
  const locale = useLocale();
  const [q, setQ] = useState('');
  const [dev, setDev] = useState<string>('');
  const [beds, setBeds] = useState<string>('');
  const [min, setMin] = useState<string>('');
  const [max, setMax] = useState<string>('');
  const [area, setArea] = useState<string>('');
  const [status, setStatus] = useState<string>('');
  const [showDevDropdown, setShowDevDropdown] = useState(false);
  const [showBedsDropdown, setShowBedsDropdown] = useState(false);
  const [showAreaDropdown, setShowAreaDropdown] = useState(false);
  const [showStatusDropdown, setShowStatusDropdown] = useState(false);

  // ÿßÿ≥ÿ™ÿÆÿ±ÿßÿ¨ ÿßŸÑŸÇŸäŸÖ ÿßŸÑŸÅÿ±ŸäÿØÿ© ŸÑŸÑŸÇŸàÿßÿ¶ŸÖ ÿßŸÑŸÖŸÜÿ®ÿ´ŸÇÿ©
  const uniqueDevelopers = useMemo(() => {
    const developers = initial.map(p => p.developer || '').filter(Boolean);
    return [...new Set(developers)].sort();
  }, [initial]);

  const uniqueBedrooms = useMemo(() => {
    const allBeds = initial.flatMap(p => p.bedrooms || []);
    return [...new Set(allBeds)].sort((a, b) => a - b);
  }, [initial]);

  const uniqueAreas = useMemo(() => {
    const areas = initial.map(p => {
      const area = p.area;
      if (typeof area === 'string') return area;
      if (area && typeof area === 'object') return area[locale] || area.en || '';
      return '';
    }).filter(Boolean);
    return [...new Set(areas)].sort();
  }, [initial, locale]);

  const uniqueStatuses = useMemo(() => {
    const statuses = initial.map(p => {
      const status = p.projectStatus;
      if (typeof status === 'string') return status;
      if (status && typeof status === 'object') return status[locale] || status.en || '';
      return '';
    }).filter(Boolean);
    return [...new Set(statuses)].sort();
  }, [initial, locale]);

  const filtered = useMemo(() => {
    return initial.filter(p => {
      const name = typeof p.projectName === 'string' ? p.projectName : (p.projectName?.[locale] || p.projectName?.en || '');
      const hitQ = !q || name.toLowerCase().includes(q.toLowerCase());
      const hitDev = !dev || (p.developer || '').toLowerCase() === dev.toLowerCase();
      const minOk = !min || (p.minPriceAED || 0) >= Number(min);
      const maxOk = !max || (p.maxPriceAED || 0) <= Number(max);
      const bedOk = !beds || (Array.isArray(p.bedrooms) && p.bedrooms.some(b => String(b) === beds));
      
      // ŸÖÿπÿßŸÑÿ¨ÿ© ÿßŸÑŸÖŸÜÿ∑ŸÇÿ©
      const projectArea = p.area;
      const areaString = typeof projectArea === 'string' ? projectArea : (projectArea?.[locale] || projectArea?.en || '');
      const areaOk = !area || areaString.toLowerCase() === area.toLowerCase();
      
      // ŸÖÿπÿßŸÑÿ¨ÿ© ÿßŸÑÿ≠ÿßŸÑÿ©
      const projectStatus = p.projectStatus;
      const statusString = typeof projectStatus === 'string' ? projectStatus : (projectStatus?.[locale] || projectStatus?.en || '');
      const statusOk = !status || statusString.toLowerCase() === status.toLowerCase();
      
      return hitQ && hitDev && minOk && maxOk && bedOk && areaOk && statusOk;
    });
  }, [initial, q, dev, min, max, beds, area, status, locale]);

  const clearFilters = () => {
    setQ('');
    setDev('');
    setBeds('');
    setMin('');
    setMax('');
    setArea('');
    setStatus('');
  };

  return (
    <div className="space-y-6">
      {/* ŸÅŸÑÿ™ÿ±ÿ© ŸÖÿ™ŸÇÿØŸÖÿ© ŸÖÿπ ŸÇŸàÿßÿ¶ŸÖ ŸÖŸÜÿ®ÿ´ŸÇÿ© */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        {/* ÿßŸÑÿ®ÿ≠ÿ´ */}
        <div className="relative">
          <input 
            className="w-full bg-zinc-900 border border-zinc-700 rounded-lg px-4 py-3 text-white placeholder-gray-400 focus:outline-none focus:border-gold transition-colors"
            placeholder={locale === 'ar' ? 'üîç ÿßÿ®ÿ≠ÿ´ ÿπŸÜ ŸÖÿ¥ÿ±Ÿàÿπ...' : 'üîç Search projects...'}
            value={q}
            onChange={e => setQ(e.target.value)}
          />
        </div>

        {/* ÿßŸÑŸÖÿ∑Ÿàÿ±ŸàŸÜ - ŸÇÿßÿ¶ŸÖÿ© ŸÖŸÜÿ®ÿ´ŸÇÿ© */}
        <div className="relative">
          <button
            className="w-full bg-zinc-900 border border-zinc-700 rounded-lg px-4 py-3 text-white text-left flex justify-between items-center hover:border-gold transition-colors"
            onClick={() => setShowDevDropdown(!showDevDropdown)}
          >
            <span>{dev || (locale === 'ar' ? 'üë∑ ÿ¨ŸÖŸäÿπ ÿßŸÑŸÖÿ∑Ÿàÿ±ŸäŸÜ' : 'üë∑ All Developers')}</span>
            <span className="text-gray-400">‚ñº</span>
          </button>
          {showDevDropdown && (
            <div className="absolute top-full left-0 right-0 mt-1 bg-zinc-800 border border-zinc-700 rounded-lg shadow-xl z-10 max-h-60 overflow-y-auto">
              <button
                className="w-full px-4 py-2 text-left text-white hover:bg-zinc-700 transition-colors"
                onClick={() => {
                  setDev('');
                  setShowDevDropdown(false);
                }}
              >
                {locale === 'ar' ? 'üë∑ ÿ¨ŸÖŸäÿπ ÿßŸÑŸÖÿ∑Ÿàÿ±ŸäŸÜ' : 'üë∑ All Developers'}
              </button>
              {uniqueDevelopers.map(developer => (
                <button
                  key={developer}
                  className="w-full px-4 py-2 text-left text-white hover:bg-zinc-700 transition-colors"
                  onClick={() => {
                    setDev(developer);
                    setShowDevDropdown(false);
                  }}
                >
                  {developer}
                </button>
              ))}
            </div>
          )}
        </div>

        {/* ÿßŸÑÿ∫ÿ±ŸÅ - ŸÇÿßÿ¶ŸÖÿ© ŸÖŸÜÿ®ÿ´ŸÇÿ© */}
        <div className="relative">
          <button
            className="w-full bg-zinc-900 border border-zinc-700 rounded-lg px-4 py-3 text-white text-left flex justify-between items-center hover:border-gold transition-colors"
            onClick={() => setShowBedsDropdown(!showBedsDropdown)}
          >
            <span>{beds || (locale === 'ar' ? 'üõèÔ∏è ÿ¨ŸÖŸäÿπ ÿßŸÑÿ∫ÿ±ŸÅ' : 'üõèÔ∏è All Bedrooms')}</span>
            <span className="text-gray-400">‚ñº</span>
          </button>
          {showBedsDropdown && (
            <div className="absolute top-full left-0 right-0 mt-1 bg-zinc-800 border border-zinc-700 rounded-lg shadow-xl z-10 max-h-60 overflow-y-auto">
              <button
                className="w-full px-4 py-2 text-left text-white hover:bg-zinc-700 transition-colors"
                onClick={() => {
                  setBeds('');
                  setShowBedsDropdown(false);
                }}
              >
                {locale === 'ar' ? 'üõèÔ∏è ÿ¨ŸÖŸäÿπ ÿßŸÑÿ∫ÿ±ŸÅ' : 'üõèÔ∏è All Bedrooms'}
              </button>
              {uniqueBedrooms.map(bed => (
                <button
                  key={bed}
                  className="w-full px-4 py-2 text-left text-white hover:bg-zinc-700 transition-colors"
                  onClick={() => {
                    setBeds(String(bed));
                    setShowBedsDropdown(false);
                  }}
                >
                  {bed} {locale === 'ar' ? 'ÿ∫ÿ±ŸÅÿ©' : 'Bedroom'}{bed > 1 ? (locale === 'ar' ? 's' : 's') : ''}
                </button>
              ))}
            </div>
          )}
        </div>

        {/* ÿßŸÑŸÖŸÜÿ∑ŸÇÿ© - ŸÇÿßÿ¶ŸÖÿ© ŸÖŸÜÿ®ÿ´ŸÇÿ© */}
        <div className="relative">
          <button
            className="w-full bg-zinc-900 border border-zinc-700 rounded-lg px-4 py-3 text-white text-left flex justify-between items-center hover:border-gold transition-colors"
            onClick={() => setShowAreaDropdown(!showAreaDropdown)}
          >
            <span>{area || (locale === 'ar' ? 'üìç ÿ¨ŸÖŸäÿπ ÿßŸÑŸÖŸÜÿßÿ∑ŸÇ' : 'üìç All Areas')}</span>
            <span className="text-gray-400">‚ñº</span>
          </button>
          {showAreaDropdown && (
            <div className="absolute top-full left-0 right-0 mt-1 bg-zinc-800 border border-zinc-700 rounded-lg shadow-xl z-10 max-h-60 overflow-y-auto">
              <button
                className="w-full px-4 py-2 text-left text-white hover:bg-zinc-700 transition-colors"
                onClick={() => {
                  setArea('');
                  setShowAreaDropdown(false);
                }}
              >
                {locale === 'ar' ? 'üìç ÿ¨ŸÖŸäÿπ ÿßŸÑŸÖŸÜÿßÿ∑ŸÇ' : 'üìç All Areas'}
              </button>
              {uniqueAreas.map(areaItem => (
                <button
                  key={areaItem}
                  className="w-full px-4 py-2 text-left text-white hover:bg-zinc-700 transition-colors"
                  onClick={() => {
                    setArea(areaItem);
                    setShowAreaDropdown(false);
                  }}
                >
                  {areaItem}
                </button>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* ÿµŸÅ ÿßŸÑŸÅŸÑÿ™ÿ±ÿ© ÿßŸÑÿ´ÿßŸÜŸä */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {/* ÿßŸÑÿ≥ÿπÿ± ÿßŸÑÿ£ÿØŸÜŸâ */}
        <div className="relative">
          <input 
            type="number"
            className="w-full bg-zinc-900 border border-zinc-700 rounded-lg px-4 py-3 text-white placeholder-gray-400 focus:outline-none focus:border-gold transition-colors"
            placeholder={locale === 'ar' ? 'üí∞ ÿßŸÑÿ≥ÿπÿ± ÿßŸÑÿ£ÿØŸÜŸâ (AED)' : 'üí∞ Min Price (AED)'}
            value={min}
            onChange={e => setMin(e.target.value)}
          />
        </div>

        {/* ÿßŸÑÿ≥ÿπÿ± ÿßŸÑÿ£ŸÇÿµŸâ */}
        <div className="relative">
          <input 
            type="number"
            className="w-full bg-zinc-900 border border-zinc-700 rounded-lg px-4 py-3 text-white placeholder-gray-400 focus:outline-none focus:border-gold transition-colors"
            placeholder={locale === 'ar' ? 'üí∞ ÿßŸÑÿ≥ÿπÿ± ÿßŸÑÿ£ŸÇÿµŸâ (AED)' : 'üí∞ Max Price (AED)'}
            value={max}
            onChange={e => setMax(e.target.value)}
          />
        </div>

        {/* ÿßŸÑÿ≠ÿßŸÑÿ© - ŸÇÿßÿ¶ŸÖÿ© ŸÖŸÜÿ®ÿ´ŸÇÿ© */}
        <div className="relative">
          <button
            className="w-full bg-zinc-900 border border-zinc-700 rounded-lg px-4 py-3 text-white text-left flex justify-between items-center hover:border-gold transition-colors"
            onClick={() => setShowStatusDropdown(!showStatusDropdown)}
          >
            <span>{status || (locale === 'ar' ? 'üìä ÿ¨ŸÖŸäÿπ ÿßŸÑÿ≠ÿßŸÑÿßÿ™' : 'üìä All Statuses')}</span>
            <span className="text-gray-400">‚ñº</span>
          </button>
          {showStatusDropdown && (
            <div className="absolute top-full left-0 right-0 mt-1 bg-zinc-800 border border-zinc-700 rounded-lg shadow-xl z-10 max-h-60 overflow-y-auto">
              <button
                className="w-full px-4 py-2 text-left text-white hover:bg-zinc-700 transition-colors"
                onClick={() => {
                  setStatus('');
                  setShowStatusDropdown(false);
                }}
              >
                {locale === 'ar' ? 'üìä ÿ¨ŸÖŸäÿπ ÿßŸÑÿ≠ÿßŸÑÿßÿ™' : 'üìä All Statuses'}
              </button>
              {uniqueStatuses.map(statusItem => (
                <button
                  key={statusItem}
                  className="w-full px-4 py-2 text-left text-white hover:bg-zinc-700 transition-colors"
                  onClick={() => {
                    setStatus(statusItem);
                    setShowStatusDropdown(false);
                  }}
                >
                  {statusItem}
                </button>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* ÿ£ÿ≤ÿ±ÿßÿ± ÿßŸÑÿ™ÿ≠ŸÉŸÖ */}
      <div className="flex justify-between items-center">
        <div className="text-white/70">
          {locale === 'ar' ? 'ÿ™ŸÖ ÿßŸÑÿπÿ´Ÿàÿ± ÿπŸÑŸâ' : 'Found'} {filtered.length} {locale === 'ar' ? 'ŸÖÿ¥ÿ±Ÿàÿπ' : 'project'}{filtered.length !== 1 ? (locale === 'ar' ? 'ÿßÿ™' : 's') : ''}
        </div>
        <LuxuryButton 
          variant="primary" 
          size="md" 
          className="rounded-lg font-semibold" 
          onClick={clearFilters}
        >
          {locale === 'ar' ? 'üóëÔ∏è ŸÖÿ≥ÿ≠ ÿßŸÑŸÉŸÑ' : 'üóëÔ∏è Clear All'}
        </LuxuryButton>
      </div>

      {/* ÿßŸÑŸÜÿ™ÿßÿ¶ÿ¨ */}
      {filtered.length === 0 ? (
        <div className="text-center py-12">
          <div className="text-6xl mb-4">üîç</div>
          <p className="text-gray-400 text-lg">
            {locale === 'ar' ? 'ŸÑŸÖ ŸÜÿπÿ´ÿ± ÿπŸÑŸâ ŸÖÿ¥ÿßÿ±Ÿäÿπ ŸÖÿ∑ÿßÿ®ŸÇÿ©.' : 'No matching projects found.'}
          </p>
          <p className="text-gray-500 mt-2">
            {locale === 'ar' ? 'ÿ¨ÿ±ÿ® ÿ™ÿ∫ŸäŸäÿ± ŸÖÿπÿßŸäŸäÿ± ÿßŸÑÿ®ÿ≠ÿ´.' : 'Try changing your search criteria.'}
          </p>
        </div>
      ) : (
        <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {filtered.map((p) => <ProjectCard key={p.slug} project={p} />)}
        </div>
      )}
    </div>
  );
}
